.work-card{
    padding: 8px;
    width: 45%;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;



}
.company-name{
    font-size: 18px;
}
.company-designation{
    font-size: 15px;
}

.work-logo{
    height: 80px;

}

.work-dates{
    color: gray;

}

.work-desc{
    text-align: left;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    --webkit-box-orient: horizontal;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80%;

}

@media only screen and (max-width: 720px) {
    .work-card{
        width: 100%;
    }
}